.container {
  text-align: center;
}

.error-message h1 {
  font-size: 10rem;
  color: #007BFF; /* Blue color for the error code */
  margin: 0;
}

.error-message p {
  font-size: 2rem;
  margin: 20px 0;
  color: #ffffff; /* White color for the text */
}

.home-link {
  display: inline-block;
  padding: 10px 20px;
  font-size: 1rem;
  color: #000; /* Black text for the link */
  background-color: #007BFF; /* Blue background for the link */
  text-decoration: none;
  border-radius: 5px;
  transition: background-color 0.3s;
}

.home-link:hover {
  background-color: #0056b3; /* Darker blue for hover effect */
}




