/* ========================================= */
/* =========register ========= */

.form-container{
  display: flex;
  align-items: center;
  justify-content: center;
  height: 70vh;
  flex-direction:column;
}


.product-info {
  display:block; /* Makes elements display in a row */
  flex-wrap: wrap; /* Allows elements to wrap to the next line if needed */
  border: 1px solid #ddd; /* Adds a border around the product info */
  padding: 10px; /* Adds some padding inside the border */
  margin: 10px; /* Adds some margin around the product info */
  text-transform: uppercase;
  box-shadow: 0 8px 6px -6px gray;
  --webkit-box-shadow: 0 8px 6px -6px gray;
  border-bottom: solid rgba(207, 109, 10, 0.731) !important;
}

.product-info h6 {
  margin: 1; /* Removes default margin from h6 elements */
  padding: 5px; /* Adds some padding inside each h6 element */
  border: 1px solid #eee; /* Adds a border around each h6 element */
  border-radius: 5px; /* Adds rounded corners to the borders */
  text-align: left;
  display: flex;
  justify-content: space-between;
}

.hd-sub{
  color: #d57508;
  padding-right: 10px;
  font-size: larger;
  font-weight: 200;
}

.product-info button {
  background-color: #007bff; /* Sets button background color */
  color: white; /* Sets button text color */
  padding: 10px 20px; /* Adds padding to the button */
  border: none; /* Removes default border from button */
  border-radius: 5px; /* Adds rounded corners to the button */
  margin-left: auto; /* Aligns button to the right */
}


.product-card-home{
  display: flex;
  flex-direction: row;
  margin-right: auto;
  margin-left: 150px;
}

.product-heading{
  font-size: 50px;
  width: fit-content;
  border-bottom: 3px solid;
  /* text-align: center; */
  /* justify-content: center; */
  margin: auto;
}

.loadMore-button{
  /* margin-left: 30px; */
  margin: auto 350px;
  width: fit-content;
}